/**
 * General constants
 */
.list-container .column .column-header {
  font-family: var(--font-family-semibold);
  font-weight: 600; }

/**
 * Mixins
 */
.list-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  position: relative;
  width: 100%; }
  .list-container .column {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: block;
    position: relative;
    flex-basis: 6.25rem; }
    .list-container .column.auto-width {
      flex-grow: 1;
      flex-shrink: 0; }
    .list-container .column.checkbox {
      flex: 0 0 2rem; }
    .list-container .column:first-child .column-header {
      padding-left: 1.25rem; }
      :root[dir='rtl'] .list-container .column:first-child .column-header {
        padding-right: 1.25rem; }
    .list-container .column:first-child .column-content {
      padding-left: 1.25rem; }
      :root[dir='rtl'] .list-container .column:first-child .column-content {
        padding-right: 1.25rem; }
    .list-container .column .column-header {
      box-sizing: border-box;
      padding: 0;
      margin: 0;
      display: block;
      position: relative;
      box-sizing: border-box;
      padding: 0;
      margin: 0;
      border: none;
      outline: none;
      background: none;
      box-shadow: none;
      text-decoration: none;
      font-size: var(--font-size-small);
      line-height: 1rem;
      padding: 0.5rem 1.25rem 0 0;
      vertical-align: bottom;
      white-space: nowrap; }
      .list-container .column .column-header:focus {
