        outline: none; }
      .list-container .column .column-header:not(:disabled) {
        cursor: pointer; }
      :root[dir='rtl'] .list-container .column .column-header {
        padding: 0.5rem 0 0 1.25rem; }
      .list-container .column .column-header.checkbox {
        padding-right: 0.75rem; }
        :root[dir='rtl'] .list-container .column .column-header.checkbox {
          padding-left: 0.75rem; }
      .list-container .column .column-header .checkbox-empty {
        height: 1rem; }
      .list-container .column .column-header.checkbox-empty {
        cursor: initial; }
      .list-container .column .column-header .sort-direction {
        margin-left: 0.5rem; }
        :root[dir='rtl'] .list-container .column .column-header .sort-direction {
          margin: 0 0.5rem 0 0; }
    .list-container .column .column-content {
