      margin-top: 0.5rem;
      white-space: nowrap; }
      :root[dir='rtl'] .list-container .column .column-content {
        padding: 1.25rem 0 0 1.25rem; }
      .list-container .column .column-content:first-child {
        padding-left: 1.25rem; }
        :root[dir='rtl'] .list-container .column .column-content:first-child {
          padding-right: 1.25rem; }
      .list-container .column .column-content.checkbox {
        padding-right: 0.75rem; }
        :root[dir='rtl'] .list-container .column .column-content.checkbox {
          padding-left: 0.75rem; }
    .list-container .column .select-all-label {
      display: none; }
