.radio-container {
  line-height: var(--line-height-small); }
  .radio-container + .radio-container:not(.columns) {
    margin-top: 0.75rem; }
  .radio-container.columns {
    display: inline-block;
    margin-right: 0.5rem; }
    :root[dir='rtl'] .radio-container.columns {
      margin-left: 0.5rem;
      margin-right: unset; }
  .radio-container input {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    position: absolute;
    top: 1px;
    left: 1px;
    width: 0px;
    height: 0px;
    border: none;
    outline: none; }
    :root[dir='rtl'] .radio-container input {
      left: unset;
      right: 1px; }
    .radio-container input:focus ~ .radio-button:not(.disabled) {
      border: 1px dashed var(--color-accent); }
  .radio-container .radio-text {
    padding-left: 0.5rem; }
    :root[dir='rtl'] .radio-container .radio-text {
      padding-left: unset;
      padding-right: 0.5rem; }
  .radio-container .radio-fill {
    position: absolute;
    left: 0.25rem;
    top: 0.25rem;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 0.25rem;
    cursor: pointer; }
    .radio-container .radio-fill.selected {
      background-color: var(--color-accent); }
      .radio-container .radio-fill.selected.disabled {
        background-color: var(--color-foreground-disabled); }
    .radio-container .radio-fill.disabled {
      cursor: not-allowed; }
  .radio-container .radio-wrapper {
    position: relative; }
  .radio-container .radio-button {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    position: relative;
    width: 1rem;
    height: 1rem;
    background-color: var(--color-content-background-primary);
    border: 1px solid var(--color-foreground-default);
    border-radius: 0.5rem;
    outline: none;
    vertical-align: top;
    cursor: pointer; }
    .radio-container .radio-button.selected {
      border: 1px solid var(--color-foreground-default); }
    .radio-container .radio-button.disabled {
      border: 1px solid var(--color-foreground-disabled);
      cursor: not-allowed; }
  .radio-container .radio-label {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-start; }
    .radio-container .radio-label.disabled {
      color: var(--color-foreground-disabled); }
    .radio-container .radio-label:not(.disabled):not(.selected):hover .radio-fill {
      background-color: var(--color-foreground-default); }
    .radio-container .radio-label:not(.disabled).selected:hover .radio-fill,
    .radio-container .radio-label:not(.disabled).selected:focus .radio-fill {
      background-color: var(--color-accent-hover); }
  .radio-container.hidden {
    display: none; }
  .radio-container:hover .radio-fill:not(.disabled):not(.selected) {
    background-color: var(--color-center-radio-hover); }
