.time-input {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  text-align: center; }

.time-input-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  position: relative;
  display: inline-block; }
  .time-input-container .time-input {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    width: 3rem;
    height: 2rem;
    outline: none;
    appearance: none;
    padding: 0 0 0 1rem;
    border-radius: 2px;
    background-color: var(--color-content-background-primary);
    cursor: pointer;
    border: 1px solid var(--color-foreground-secondary); }
    :root[dir='rtl'] .time-input-container .time-input {
      padding: 0 1rem 0 0; }
    .time-input-container .time-input option {
      height: 2rem;
      padding: 0px 1rem; }
      .time-input-container .time-input option:checked {
        background-color: var(--color-state-selected); }
    .time-input-container .time-input:hover, .time-input-container .time-input:focus, .time-input-container .time-input:active {
      border: 1px solid var(--color-accent-hover); }
    .time-input-container .time-input:disabled {
      color: var(--color-foreground-disabled);
      border: 1px solid var(--color-foreground-disabled);
      background-color: var(--color-state-hover);
      cursor: not-allowed; }
    .time-input-container .time-input.error:not(:disabled) {
      border: 1px solid var(--color-status-danger); }
  .time-input-container .time-input-period {
    padding: 0 0 0 0.75rem; }
    :root[dir='rtl'] .time-input-container .time-input-period {
      padding: 0 0.75rem 0 0; }
