/**
 * General constants
 */
/**
 * Mixins
 */
.checkbox-button {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  width: 1rem;
  height: 1rem;
  background-color: var(--color-content-background-primary);
  outline: none;
  vertical-align: top;
  border-radius: 2px;
  transition: background-color 0.2s ease-out;
  border: 1px solid var(--color-foreground-secondary); }

.checkbox-text {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  vertical-align: top;
  padding-left: 0.5rem; }
  :root[dir='rtl'] .checkbox-text {
    padding-left: unset;
    padding-right: 0.5rem; }

.checkbox-fill {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: none;
  position: absolute;
  left: 0.375rem;
  top: 0.375rem;
  width: 0.25rem;
  height: 0.25rem;
  background-color: var(--color-accent-foreground); }

.checkbox-label {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative; }

.checkbox-checkmark {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: none;
  position: absolute;
  line-height: var(--line-height-xsmall);
  color: var(--color-foreground-default);
  left: 0;
  cursor: pointer; }
  :root[dir='rtl'] .checkbox-checkmark {
    left: unset;
    right: 0; }

.checkbox-container:hover:not(.disabled):not(.selected):not(.indeterminate) .checkbox-button {
  border: 1px solid var(--color-foreground-default); }

.checkbox-container:hover:not(.disabled):not(.indeterminate) .checkbox-checkmark {
  display: inline-block; }

.checkbox-container:hover.selected:not(.disabled) .checkbox-checkmark {
  color: var(--color-state-selected-hover); }

.checkbox-container:hover.indeterminate:not(.disabled) .checkbox-fill {
  background-color: var(--color-state-selected-hover); }

.checkbox-container.selected .checkbox-button {
  border: none;
  background-color: var(--color-accent); }

.checkbox-container.selected .checkbox-checkmark {
  display: inline-block;
  color: var(--color-accent-foreground); }

.checkbox-container.selected.disabled .checkbox-button {
  border: none;
  background-color: var(--color-state-disabled); }

.checkbox-container.indeterminate .checkbox-checkmark {
  display: none; }

.checkbox-container.indeterminate .checkbox-button {
  background-color: var(--color-accent);
  border: none;
  cursor: pointer; }

.checkbox-container.indeterminate .checkbox-fill {
  display: inline-block;
  cursor: pointer; }

.checkbox-container.indeterminate.disabled .checkbox-button {
  border: none;
  background-color: var(--color-state-disabled); }

.checkbox-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: block;
  position: relative;
  line-height: var(--line-height-small);
  color: var(--color-foreground-default); }
  .checkbox-container + .checkbox-container:not(.columns) {
    margin-top: 0.75rem; }
  .checkbox-container.columns {
    display: inline-block;
    margin-right: 0.5rem; }
    :root[dir='rtl'] .checkbox-container.columns {
      margin-right: 0;
      margin-left: 0.5rem; }
  .checkbox-container input {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    position: absolute;
    left: 1px;
    width: 0px;
    height: 0px;
    border: none;
    outline: none; }
    :root[dir='rtl'] .checkbox-container input {
      left: unset;
      right: 1px; }
  .checkbox-container:not(.disabled) input:focus + .checkbox-button {
    outline: 1px dashed var(--color-accent);
    outline-offset: -1px; }
  .checkbox-container:not(.disabled).selected input:focus + .checkbox-button, .checkbox-container:not(.disabled).indeterminate input:focus + .checkbox-button {
    outline: 1px dashed var(--color-accent-foreground);
    outline-offset: -1px; }
  .checkbox-container.disabled {
    color: var(--color-foreground-disabled); }
    .checkbox-container.disabled .checkbox-button {
      border: 1px solid var(--color-foreground-disabled);
      background-color: var(--color-state-disabled);
      cursor: not-allowed; }
    .checkbox-container.disabled .checkbox-fill {
      background-color: var(--color-foreground-disabled);
      cursor: not-allowed; }
    .checkbox-container.disabled .checkbox-checkmark {
      color: var(--color-foreground-disabled);
      cursor: not-allowed; }
