 */
/**
 * Mixins
 */
.datetime-field .date-picker {
  padding-right: 0.25rem; }
  :root[dir='rtl'] .datetime-field .date-picker {
    padding-right: unset;
    padding-left: 0.25rem; }

.datetime-field .time-picker {
  padding-left: 0.25rem; }
  :root[dir='rtl'] .datetime-field .time-picker {
    padding-left: unset;
    padding-right: 0.25rem; }

.datetime-field .field-content {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  position: relative; }

.datetime-field .field-date {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  flex: 1 0 auto; }

.datetime-field .field-time {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  flex: 0 0 auto; }
