.date-picker-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%; }
  .date-picker-container .date-picker-input-container {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: block;
    position: relative;
    flex-grow: 1; }
  .date-picker-container .date-picker-input {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: block;
    position: relative;
    width: 100%;
    height: 2rem;
    padding: 0.5rem 2rem 0.5rem 0.75rem;
    outline: none;
    border: 1px solid var(--color-foreground-secondary);
    background-color: var(--color-content-background-primary);
    border-radius: 2px; }
    :root[dir='rtl'] .date-picker-container .date-picker-input {
      padding: 0.5rem 0.75rem 0.5rem 2rem; }
    .date-picker-container .date-picker-input:hover, .date-picker-container .date-picker-input:focus, .date-picker-container .date-picker-input:active {
      border: 1px solid var(--color-accent-hover); }
    .date-picker-container .date-picker-input:disabled {
      color: var(--color-foreground-disabled);
      background-color: var(--color-state-disabled);
      border: 1px solid var(--color-foreground-disabled);
      cursor: not-allowed; }
      .date-picker-container .date-picker-input:disabled::placeholder {
        color: var(--color-foreground-disabled); }
    .date-picker-container .date-picker-input::placeholder {
      color: var(--color-foreground-inactive); }
    .date-picker-container .date-picker-input.error:not(:disabled) {
      border: 1px solid var(--color-status-danger); }
    .date-picker-container .date-picker-input::-ms-clear {
      display: none; }
    .date-picker-container .date-picker-input:invalid {
      box-shadow: none; }
  .date-picker-container .date-picker-calendar-icon {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    position: absolute;
    font-size: var(--font-size-small);
    top: 1px;
    right: 1px;
    border: none;
    background-color: transparent;
    height: calc(2rem - 2.5px); }
    :root[dir='rtl'] .date-picker-container .date-picker-calendar-icon {
      right: unset;
      left: 1px; }
    .date-picker-container .date-picker-calendar-icon:focus:not(:disabled) {
      outline-offset: -2px; }

.date-picker-dropdown {
  padding-bottom: 0.5rem;
  left: 0;
  width: 16.25rem;
  display: inline-block;
  z-index: 9500;
  position: absolute;
  border-radius: 2px;
  background-color: var(--color-content-background-primary);
  border: 1px solid var(--color-accent-hover); }
  .date-picker-dropdown:not(.above) {
    top: calc(100% + 5px); }
  .date-picker-dropdown.above {
    bottom: calc(100% + 5px); }
  :root[dir='rtl'] .date-picker-dropdown {
    left: unset;
    right: 0; }
  :global(.md-dropdown-container) .date-picker-dropdown {
    background-color: var(--color-content-background-primary);
    border: 1px solid var(--color-accent-hover); }
  .date-picker-dropdown .date-picker-dropdown-triangle {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: block;
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    left: 1rem; }
    :root[dir='rtl'] .date-picker-dropdown .date-picker-dropdown-triangle {
      left: unset;
      right: 1rem; }
  .date-picker-dropdown:not(.above) .date-picker-dropdown-triangle {
    bottom: calc(100% - 3px);
    background-color: var(--color-content-background-primary);
    border-left: 1px solid var(--color-accent-hover);
    border-top: 1px solid var(--color-accent-hover);
    transform: rotateZ(45deg); }
  .date-picker-dropdown.above .date-picker-dropdown-triangle {
    top: calc(100% - 3px);
    background-color: var(--color-content-background-primary);
    border-right: 1px solid var(--color-accent-hover);
    border-bottom: 1px solid var(--color-accent-hover);
    transform: rotateZ(45deg); }
