.toggle {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  outline: none;
  line-height: 0.875rem;
  background-color: transparent;
  border: none; }
  .toggle:not(.disabled):not(.toggle-on):hover .toggle-switch-container, .toggle:not(.disabled):not(.toggle-on):focus .toggle-switch-container {
    border: 1px solid var(--color-foreground-default); }
  .toggle:not(.disabled):not(.toggle-on):hover .toggle-switch, .toggle:not(.disabled):not(.toggle-on):focus .toggle-switch {
    background-color: var(--color-foreground-default); }
  .toggle:focus {
    outline: 1px dashed var(--color-accent);
    outline-offset: 1px; }
  .toggle.toggle-on:not(.disabled) .toggle-switch-container {
    border: 1px solid var(--color-accent);
    background-color: var(--color-accent); }
    .toggle.toggle-on:not(.disabled) .toggle-switch-container:hover {
      border: 1px solid var(--color-accent-hover);
      background-color: var(--color-accent-hover); }
  .toggle.toggle-on .toggle-switch {
    left: 25px;
    background-color: var(--color-accent-foreground); }
    :root[dir='rtl'] .toggle.toggle-on .toggle-switch {
      left: unset;
      right: 25px; }
  .toggle.toggle-on.disabled .toggle-switch-container {
    border: 1px solid var(--color-foreground-disabled);
    background-color: var(--color-foreground-disabled); }
  .toggle.toggle-on.disabled .toggle-switch {
    background-color: var(--color-state-disabled); }
  .toggle.disabled .toggle-switch-container {
    border: 1px solid var(--color-foreground-disabled);
    cursor: not-allowed; }
  .toggle.disabled .toggle-switch {
    background-color: var(--color-foreground-disabled); }
  .toggle.disabled .toggle-label {
    color: var(--color-foreground-disabled); }

.toggle-switch-container {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid var(--color-foreground-inactive);
  background-color: transparent;
  border-radius: 0.625rem;
  width: 2.5rem;
  height: 1.25rem;
  cursor: pointer; }

.toggle-switch {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: block;
  position: absolute;
  left: 0.3125rem;
  top: 0.3125rem;
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 5px;
  background-color: var(--color-foreground-inactive);
  transition: 75ms ease-in-out;
  pointer-events: none; }
  :root[dir='rtl'] .toggle-switch {
    left: unset;
    right: 0.3125rem; }

.toggle-label {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  margin-left: 0.5rem;
  font-family: var(--font-family-default);
  font-size: var(--font-size-default);
  vertical-align: middle; }
  :root[dir='rtl'] .toggle-label {
    margin: 0 0.5rem 0 0; }
